import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { WorkPermitComponent } from './work-permit.component';
import { WorkpermitSetupComponent } from './components/workpermit-setup/workpermit-setup.component';
import { WorkpermitApprovalComponent } from './components/workpermit-approval/workpermit-approval.component';
import { WorkpermitCreateComponent } from './components/workpermit-create/workpermit-create.component';
import { WorkpermitSettingComponent } from './components/workpermit-setting/workpermit-setting.component';

const routes: Routes = [
  { path: '', redirectTo: 'setup', pathMatch: 'full'},
  { path: 'setup', component: WorkpermitSetupComponent},
  { path: 'approval', component: WorkpermitApprovalComponent},
  { path: 'create', component: WorkpermitCreateComponent},
  { path: '**', redirectTo: 'setup', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WorkPermitRoutingModule { }
